import React, { Component } from "react";


import TopNavigation from "./layouts/TopNavigation";
//import SimpleBottomNavigation from "./components/BottomNav";
//import myimage from "../src/images/Devpict.jpg";
import Frontpage from './components/Frontpage';
import { BrowserRouter, Switch } from "react-router-dom";
import Route from "react-router-dom/Route";
import MyProfile from "./components/MyProfile";
//import MainPageMenus from "./components/MainPageMenu";
import Allexplained from "./Allexplained";
//import ResponsiveDrawer from './components/ResponisveMenu'

import ChatIcon from '@material-ui/icons/Chat';
import PhoneIcon from '@material-ui/icons/Phone';
import EmailIcon from '@material-ui/icons/Email';
import LanguageIcon from '@material-ui/icons/Language';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import BottomNavModal from './components/BottomNavModal'
export default class App extends Component {
  constructor(props) {
    super(props);

   
    this.state = {
    modalTitle:'',
      modal: false,
      messagetext:'',
    };
 
    this.toggle1 = this.toggle1.bind(this);
  }
 
  toggle1() {
    this.setState(prevState => ({
      modal: !prevState.modal
    }));
  }
  handleClick=(message,title)=>{
    this.toggle1()
    this.setState({
      messagetext:message,
      modalTitle:title,
    })
    }
 
  render() {
    return (
      <div>
        <Modal isOpen={this.state.modal} toggle={this.toggle1} className={this.props.className}>
          <ModalHeader toggle={this.toggle1}>{this.state.modalTitle}</ModalHeader>
          <ModalBody>
          <BottomNavModal textmessage={this.state.messagetext}/>
          </ModalBody>
          <ModalFooter>
         
            <Button color="secondary" onClick={this.toggle1}>Cancel</Button>
          </ModalFooter>
        </Modal>
        <div className="navigationCon">
          <TopNavigation />
        </div>

        <BrowserRouter>
          <div>
            <Switch>
              <Route exact path="/" component={Frontpage} />
              <Route exact path="/MyProfile/" component={MyProfile} />
              <Route exact path="/Allexplained/" component={Allexplained} />
            </Switch>
          </div>
        </BrowserRouter>
        <div className='bottomNav'>
          <div className='bottomNavContent' onClick={()=>{this.handleClick('website','Visit my sample website')}}>
          <LanguageIcon/>
         <h1 className='bottomNavlabel'>Project</h1>
          </div>
          <div className='bottomNavContent'onClick={()=>{this.handleClick('chat','I am happy to chat with you mam/sir')}}>
       < ChatIcon/>
         <h1 className='bottomNavlabel'>Chat me</h1>
          </div>
          <div className='bottomNavContent' onClick={()=>{this.handleClick('email','I am happy to serve you mam/sir')}}>
         <EmailIcon/>
         <h1 className='bottomNavlabel'>Email me</h1>
          </div>
          <div className='bottomNavContent' onClick={()=>{this.handleClick('call','I will be glad to have your call mam/sir')}}>
            <PhoneIcon/>
        
         <h1 className='bottomNavlabel'>Call me</h1>
          </div>
        </div>
       
      </div>
    );
  }
}
